    find_chatIndex_inChat_BD(project_ID) {
        let findedIndex = -1;
        if (GLOB_DATA.chatList_VECTOR) findedIndex = GLOB_DATA.chatList_VECTOR.findIndex(item => item.project_ID == project_ID);
        return findedIndex;
    }

    //----------------------------
    get_object_Individual_WasReadEvents_forCurrent_Project(project_INDEX_inGlobData) {
        let returnData = null;
        // console.log("project_INDEX_inGlobData= "+project_INDEX_inGlobData);
        // console.log("GLOB_DATA.bisData_VECTOR= ");
        // console.log(GLOB_DATA.bisData_VECTOR);

        if (GLOB_DATA.bisData_VECTOR.projects[project_INDEX_inGlobData].time_individual_wasReadEvents) {
            returnData = GLOB_DATA.bisData_VECTOR.projects[project_INDEX_inGlobData].time_individual_wasReadEvents;
        }
        return returnData;
    }
    //----------------------------
    //     WasReadEvents   
    get_object_Individual_WasReadEvents_forCurrent_subProject(project_INDEX_inGlobData, subProject_ID) {
        let returnData = null;

        // console.log("project_INDEX_inGlobData= " + project_INDEX_inGlobData);
        // console.log(GLOB_DATA.bisData_VECTOR.projects[project_INDEX_inGlobData]);

        if (
            (GLOB_DATA.bisData_VECTOR.projects[project_INDEX_inGlobData]?.time_individual_wasReadEvents?.subProjects_individual_WAS_READ_EVENTS?.length > 0)
        ) {
            //   
            let findIndex = GLOB_DATA.bisData_VECTOR.projects[project_INDEX_inGlobData].time_individual_wasReadEvents.subProjects_individual_WAS_READ_EVENTS.findIndex(item => item.subProject_ID === subProject_ID);

            returnData = GLOB_DATA.bisData_VECTOR.projects[project_INDEX_inGlobData].time_individual_wasReadEvents.subProjects_individual_WAS_READ_EVENTS[findIndex];
        }

        return returnData;
    }
    //----------------------------
    get___OR_ADD_AND_GET___object_Individual_WasReadEvents_forCurrent_subProject(project_INDEX_inGlobData, subProject_ID) {
        // console.log(" get___OR_ADD_AND_GET___object_Individual_WasReadEvents_forCurrent_subProject");
        // console.log(" project_INDEX_inGlobData=" + project_INDEX_inGlobData + " subProject_ID=" + subProject_ID);

        let object_Individual_WasReadEvents_forCurrent_subProject = this.get_object_Individual_WasReadEvents_forCurrent_subProject(project_INDEX_inGlobData, subProject_ID);

        //        -       

        if (object_Individual_WasReadEvents_forCurrent_subProject === null
            || object_Individual_WasReadEvents_forCurrent_subProject === undefined
        ) {
            //  -        
            let newObject_SubProjectEvents_inUserReestr = JSON.parse(
                JSON.stringify(GLOB_DATA.ShablonFromServer___SubProjectEvents_inUserReestr)
            );
            //    ""  project_ID
            newObject_SubProjectEvents_inUserReestr.subProject_ID = subProject_ID;

            console.log("project_INDEX_inGlobData=" + project_INDEX_inGlobData);
            console.log("  subProjects_individual_WAS_READ_EVENTS.push, newObject_SubProjectEvents_inUserReestr=");
            console.log(newObject_SubProjectEvents_inUserReestr);

            //    
            GLOB_DATA.bisData_VECTOR.projects[project_INDEX_inGlobData].time_individual_wasReadEvents.subProjects_individual_WAS_READ_EVENTS.push(newObject_SubProjectEvents_inUserReestr);
            //   -         ,          
            GLOB_DATA.update_bisData_Redux(GLOB_DATA.bisData_VECTOR);

            //    


            object_Individual_WasReadEvents_forCurrent_subProject = this.get_object_Individual_WasReadEvents_forCurrent_subProject(project_INDEX_inGlobData, subProject_ID);
        }

        return object_Individual_WasReadEvents_forCurrent_subProject;
    }

