    globCorpIndication_outputInRedux() {

        if (GLOB_DATA.bisData_VECTOR) {
            let new_corpIndication = {
                newMessages: false,
                newSettings: false,
                deadline: 0,
                subProjects_newMessages: false,
                subProjects_newSettings: false,
                subProject_deadline: 0,
            }

            //  / ,    ,   .           ,    
            let new_corpList_indication = JSON.parse(JSON.stringify(this.get_fullCorpList()));

            // console.log("new_corpList_indication=");
            // console.log(new_corpList_indication);

            //        
            GLOB_DATA.bisData_VECTOR.projects.forEach(
                (item, index) => {
                    let indication_project_newMessages = this.need_Indication_newMessage_chat_current_mainProject(index);
                    let indication_project_newSettings = this.need_Indication_newSettings_current_mainProject(index);
                    let indication_project_deadline = this.needIndication_Deadline_mainProject(index);

                    let parentIndication_forChat_subProjects = this.need_ParentIndication_forChat_subProjects(index);
                    let parentIndication_newSettings_subProjects = this.need_ParentIndication_newSettings_subProjects(index);
                    let parentIndication_deadline_subProjects = this.need_ParentIndication_deadline_subProjects(index);

                    //   ,    
                    let corpAccountIndex = new_corpList_indication.findIndex(
                        (item_2 => item_2.corpAccount_ID === item.parentCorpAccount_ID)
                        &&
                        (item_2 => item_2.defaultAdminForThisProject === item.defaultAdminForThisProject)
                    );

                    //     
                    //   
                    if (indication_project_newMessages === true) {
                        //    
                        new_corpIndication.newMessages = true;
                        //    corpList
                        if (new_corpList_indication) {
                            if (corpAccountIndex != null && corpAccountIndex >= 0) {
                                new_corpList_indication[corpAccountIndex].indication_newMessages = true;
                            }
                        }
                    }

                    //     
                    //   
                    if (indication_project_newSettings === true) {
                        //    
                        new_corpIndication.newSettings = true;
                        //    corpList
                        if (new_corpList_indication) {
                            if (corpAccountIndex != null && corpAccountIndex >= 0) {
                                new_corpList_indication[corpAccountIndex].indication_newSettings = true;
                            }
                        }
                    }

                    //   
                    //   
                    if (indication_project_deadline > 1     //          
                        && (indication_project_deadline > new_corpIndication.deadline) // ,    ,         
                    ) {
                        //    
                        new_corpIndication.deadline = indication_project_deadline;
                        //    corpList
                        if (new_corpList_indication) {
                            if (corpAccountIndex != null && corpAccountIndex >= 0) {
                                new_corpList_indication[corpAccountIndex].indication_project_deadline = indication_project_deadline;
                            }
                        }
                    }


                    // ==================================
                    //     
                    //   
                    if (parentIndication_forChat_subProjects === true) {
                        //    
                        new_corpIndication.subProjects_newMessages = true;
                        //    corpList
                        if (new_corpList_indication) {
                            if (corpAccountIndex != null && corpAccountIndex >= 0) {
                                new_corpList_indication[corpAccountIndex].indication_subProject_newMessages = true;
                            }
                        }
                    }

                    //     
                    //   
                    if (parentIndication_newSettings_subProjects === true) {
                        //    
                        new_corpIndication.subProjects_newSettings = true;
                        //    corpList
                        if (new_corpList_indication) {
                            if (corpAccountIndex != null && corpAccountIndex >= 0) {
                                new_corpList_indication[corpAccountIndex].indication_subProject_newSettings = true;
                            }
                        }
                    }

                    //    
                    //   

                    if (parentIndication_deadline_subProjects > 1  //          
                        && parentIndication_deadline_subProjects > new_corpIndication.subProject_deadline
                    ) {
                        //    
                        new_corpIndication.subProject_deadline = parentIndication_deadline_subProjects;
                        //    corpList
                        if (new_corpList_indication) {
                            if (corpAccountIndex != null && corpAccountIndex >= 0) {
                                new_corpList_indication[corpAccountIndex].indication_subProject_deadline = parentIndication_deadline_subProjects;
                            }
                        }
                    }
                });

            // console.log("  new_corpIndication= ");
            // console.log(new_corpIndication);
            // console.log("  new_corpList_indication= ");
            // console.log(new_corpList_indication);

            //   
            GLOB_DATA.update_corpIndication_Redux(new_corpIndication);
            //   
            GLOB_DATA.update_corpList_indication_Redux(new_corpList_indication)
        }
    }

