    //----------------------------

    check_VerticalOverflow(ref_currentBlock) {
        let isOverflow = false;
        const currentElement = ref_currentBlock.current;
        if (currentElement) {
            isOverflow = currentElement.scrollHeight > currentElement.clientHeight;
        }
        return isOverflow;
    }

    //----------------------------

    check_GorizontalOverflow(ref_currentBlock) {
        let isOverflow = false;
        const currentElement = ref_currentBlock.current;
        if (currentElement) {
            isOverflow = currentElement.scrollWidth > currentElement.clientWidth;
        }
        return isOverflow;
    }

    //----------------------------

    checkAndSet_verticalOverflowBlock(ref_currentBlock, prevValue, funSetOverfkow) {
        let isOverflow = false;
        const currentElement = ref_currentBlock.current;
        if (currentElement) {
            isOverflow = currentElement.scrollHeight > currentElement.clientHeight;
        }

        if (isOverflow != prevValue) {
            //   ,  ResizeObserver ,         
            setTimeout(() => {
                funSetOverfkow(isOverflow);
            }, 20);
        }

    }

    //----------------------------

    checkAndSet_gorizontalOverflowBlock(ref_currentBlock, prevValue, funSetOverfkow) {
        let isOverflow = false;
        const currentElement = ref_currentBlock.current;
        if (currentElement) {
            isOverflow = currentElement.scrollWidth > currentElement.clientWidth;
        }

        if (isOverflow != prevValue) {
            //   ,  ResizeObserver ,         
            setTimeout(() => {
                funSetOverfkow(isOverflow);
            }, 0);

        }

        // console.log(" checkAndSet_gorizontalOverflowBlock");
        //  console.log(" checkAndSet_gorizontalOverflowBlock");
    }
















