export function StatusBar(props) {

    // console.log("props.status= " + props.status);

    if (props.status > 0) //   ,     "0"    
        return (
            <div className={props.status === "100" ? "StatusBar statusBar_100" : "StatusBar"}
                style={{
                    display: "flex",
                    flexDirection: "row",
                    alignItems: "flex-end",
                    // width: "10px",
                    width: "100%",
                    height: "100%",
                    borderTopLeftRadius: "6px",
                    borderBottomRightRadius: "6px",
                    overflow: "hidden",
                }}

                title={"Status " + props.status + "%"}
            >
                <div className="statusBarProgressLine"
                    style={{
                        display: "flex",
                        flexDirection: "row",
                        height: props.position == "gorizontal" ? "100%" : props.status + "%",
                        width: props.position == "gorizontal" ? props.status + "%" : "100%",
                    }}
                >
                </div>
            </div>
        )
    else return null
}

