import requestService_forLongPulling from "./requestService_forLongPulling.js";
import { GLOB_DATA } from "../GLOB_DATA.js";
import BisGlobFunctions from './bisGlobFunctions.js';
import mConfig from '../config.js';
// let controller = new AbortController(); //     fetch
// export async function mBreackFetch() { controller.abort() }; //     fetch  bis-menu,    fetch-

// let lastReqID = null;
// function mSetNewlastReqID(newID) { lastReqID = newID }  //       Fetch-

//    LongPoolling:
async function fullTimeSubscriber(user_Email, current_RequestID) {

    if (!GLOB_DATA.accessToken_readOnly) {
        console.log("=================   fullTimeSubscriber -   accessToken");
        return;
    }

    if (GLOB_DATA.repeatingReq_ID___mainProcess != current_RequestID) return;

    let postDataToServer = {
        user_Email: user_Email,
    }

    let mURL = mConfig.serverPort + "/subscribeFullTime";
    try {
        fetch(mURL, {
            method: 'post',
            headers: {
                accesstoken: GLOB_DATA.accessToken_readOnly,
                'Content-Type': 'application/json',
            },
            body: JSON.stringify({
                postDataToServer,
            })
        })
            //     
            .then(res => {
                //     401
                BisGlobFunctions.control_401_res(res, postDataToServer);
                return res;
            })
            .then((res) => {
                console.log("====================");
                console.log("    fullTimeSubscriber, res= ");
                console.log(res);
                console.log("====================");
                return res;
            })
            .then(res => res.json())
            .then((res) => {
                //   longPulling        
                // console.log(" ");
                fullTimeSubscriber(user_Email, current_RequestID);
                //     "dataFromServer"     
                requestService_forLongPulling(res.dataFromServer, current_RequestID);
            })
            .catch((error_2) => {
                //      subscribeFullTime   500 .
                setTimeout(()=>{
                    console.log("  subscribeFullTime");
                    fullTimeSubscriber(user_Email, current_RequestID);
                }, 500)
            })



    } catch (error) {
        console.log(" Fetch-  " + error);
    }
}
//--------------------------------
export { fullTimeSubscriber,  /* lastReqID, mSetNewlastReqID */ }
