//       
//   ,       .       "inputUnicodOfRequest_getUsersOnlineStatus",             .
async function getUsersOnlineStatusFromServer_forCurrentProject(
    project_ID,
    knownProjectIndex_inServer,
    inputUnicodOfRequest_getUsersOnlineStatus) {

    // console.log(" getUsersOnlineStatusFromServer_forCurrentProject, accessToken= ");
    // console.log(GLOB_DATA.accessToken_readOnly);
    // console.log("project_ID= " + project_ID);
    // console.log("active_project_INDEX_FromRedux_readOnly= " + GLOB_DATA.active_project_INDEX_FromRedux_readOnly);



    if (!GLOB_DATA.accessToken_readOnly) {
        // console.log("=================   getUsersOnlineStatusFromServer_forCurrentProject -   accessToken");
        return;
    }

    if (!project_ID) return;
    //       
    if (!(GLOB_DATA.active_project_INDEX_FromRedux_readOnly != null && GLOB_DATA.active_project_INDEX_FromRedux_readOnly >= 0)) {
        // console.log("=================   getUsersOnlineStatusFromServer_forCurrentProject");
        return;
    }

    let postDataToServer = {
        project_ID: project_ID,
        knownProjectIndex_inServer: knownProjectIndex_inServer
    }
    let mURL = mConfig.serverPort + "/getUsersOnlineStatusFromServer_forCurrentProject";

    try {
        fetch(mURL, {
            method: 'post',
            headers: {
                accesstoken: GLOB_DATA.accessToken_readOnly,
                'Content-Type': 'application/json',
            },
            body: JSON.stringify({
                postDataToServer
            })
        })
            .then(res => res.json())
            .then((res) => {
                // console.log("  psevdoServer  getUsersOnlineStatusFromServer_forContactListCurrentAdmin: ");
                // console.log(res);
                // ,     
                if (GLOB_DATA.unicodOfRequest_getUsersOnlineStatus == inputUnicodOfRequest_getUsersOnlineStatus) {

                    GLOB_DATA.update_usersOnlineStatusRedux(res);

                    setTimeout(() => {
                        // console.log("   getUsersOnlineStatusFromServer_forContactListCurrentAdmin - setTimeout 60000");
                        getUsersOnlineStatusFromServer_forCurrentProject(project_ID, knownProjectIndex_inServer, inputUnicodOfRequest_getUsersOnlineStatus);
                    }, 60000) // 
                }
                else {
                    // console.log("     -  ");
                }
            })

    } catch (error) {
        console.log("  getUsersOnlineStatusFromServer_forCurrentProject");
        console.log(error);
        setTimeout(() => {
            console.log("  getUsersOnlineStatusFromServer_forCurrentProject  error 5000ms");
            getUsersOnlineStatusFromServer_forCurrentProject(project_ID, knownProjectIndex_inServer, inputUnicodOfRequest_getUsersOnlineStatus);
        }, 5000)  // 
    }
}

