

import { GLOB_DATA } from "../GLOB_DATA.js";

import BisGlobFunctions from "./bisGlobFunctions.js";

async function requestService_forLongPulling(dataFromServer, current_RequestID) {

    if (GLOB_DATA.repeatingReq_ID___mainProcess != current_RequestID) return;

    if ((dataFromServer === undefined) || (dataFromServer === null)) return;

    switch (dataFromServer.resEndPoint) {

        case "addNewProject_PS":
            {
                // console.log("resEndPoint= " + dataFromServer.resEndPoint);
                // mCallBack_DispatchUpdateData(dataFromServer.dataBD_fromServer);
                break;
            }

        case "newMessageChatProject": {
            console.log(" ========  - newMessageChatProject, dataFromServer= ");
            console.log(dataFromServer);
            let find_chatIndex = GLOB_DATA.chatList_VECTOR.findIndex(item => item.project_ID === dataFromServer.project_ID);
            if (find_chatIndex >= 0) {
                console.log("  ,      ");
                GLOB_DATA.chatList_VECTOR[find_chatIndex].messages.
                    push(dataFromServer.newMessage);
                //  
                GLOB_DATA.update_chatList_Redux(GLOB_DATA.chatList_VECTOR);
            }
            else {
                console.log("   ,   ");
            }

            //           
            let find_Project_index = GLOB_DATA.bisData_VECTOR.projects.findIndex(item => item.project_ID === dataFromServer.project_ID);

            if (find_Project_index != null && find_Project_index >= 0) {
                GLOB_DATA.bisData_VECTOR.projects[find_Project_index].time_Update_including_Objects.time_Update_chat = dataFromServer.newMessage.timeOfCreate;
                GLOB_DATA.update_bisData_Redux(GLOB_DATA.bisData_VECTOR);
            }

            //   -          
            if (GLOB_DATA.soundOnStatus_readOnly == true && GLOB_DATA.currentUser_readOnly != dataFromServer.newMessage.autor) {
                BisGlobFunctions.sound_01();
            }

            break;
        }

        case "was_added_new_sub_Project":
            {
                BisGlobFunctions.add_new_subProject_localData(dataFromServer);
                break;
            }

        case "newMessageChat_sub_Project":
            {
                console.log(" ========  - newMessageChat_sub_Project, dataFromServer= ");
                console.log(dataFromServer);
                let find_chatIndex = BisGlobFunctions.find_chatIndex_inChat_BD(dataFromServer.subProject_ID);

                if (find_chatIndex >= 0) {
                    console.log("  ,      ");
                    GLOB_DATA.chatList_VECTOR[find_chatIndex].messages.
                        push(dataFromServer.newMessage);
                    //  
                    GLOB_DATA.update_chatList_Redux(GLOB_DATA.chatList_VECTOR);
                }
                else {
                    console.log("   ,   ");
                }

                //           
                let find_project_INDEX = BisGlobFunctions.find_Project_index_in_BD(dataFromServer.project_ID);

                if (find_project_INDEX >= 0) {
                    let find_subProject_INDEX = BisGlobFunctions.find_subProject_index_in_BD(find_project_INDEX, dataFromServer.subProject_ID);

                    if (find_subProject_INDEX >= 0) {
                        //    
                        GLOB_DATA.bisData_VECTOR.projects[find_project_INDEX].subProjects[find_subProject_INDEX].time_Update_including_Objects_SUBPROJECT.time_Update_chat = dataFromServer.newMessage.timeOfCreate;
                        //  
                        GLOB_DATA.update_bisData_Redux(GLOB_DATA.bisData_VECTOR);
                    }
                }

                break;
            }

        case "set_subProject_settings":
            {
                let find_project_INDEX = BisGlobFunctions.find_Project_index_in_BD(dataFromServer.project_ID);
                if (find_project_INDEX >= 0) {
                    let find_subProject_INDEX = BisGlobFunctions.find_subProject_index_in_BD(find_project_INDEX, dataFromServer.subProject_ID);

                    if (find_subProject_INDEX >= 0) {
                        //   
                        GLOB_DATA.bisData_VECTOR.projects[find_project_INDEX].subProjects[find_subProject_INDEX].subProjectSettings = dataFromServer.subProjectSettings;

                        //         
                        GLOB_DATA.bisData_VECTOR.projects[find_project_INDEX].subProjects[find_subProject_INDEX].time_Update_including_Objects_SUBPROJECT.time_Update_subProjectSettings = Date.now();

                        //       "  " -  ,        -        
                        if (GLOB_DATA.currentUser_readOnly == dataFromServer.user_Email) {
                            let object_Individual_WasReadEvents_forCurrent_subProject = BisGlobFunctions.get_object_Individual_WasReadEvents_forCurrent_subProject(find_project_INDEX, dataFromServer.subProject_ID);

                            object_Individual_WasReadEvents_forCurrent_subProject.time_wasRead_subProjectSettings = Date.now();

                            console.log("      ");
                        }

                        //  
                        GLOB_DATA.update_bisData_Redux(GLOB_DATA.bisData_VECTOR);
                    }
                }

                break;
            }

        case "update_ofResponsibleList_subProject":
            {
                let find_project_INDEX = BisGlobFunctions.find_Project_index_in_BD(dataFromServer.project_ID);
                if (find_project_INDEX >= 0) {
                    let find_subProject_INDEX = BisGlobFunctions.find_subProject_index_in_BD(find_project_INDEX, dataFromServer.subProject_ID);

                    if (find_subProject_INDEX >= 0) {
                        //   
                        GLOB_DATA.bisData_VECTOR.projects[find_project_INDEX].subProjects[find_subProject_INDEX].teamList_ofResponsible_subProject = dataFromServer.teamList_ofResponsible_subProject;


                        //         
                        GLOB_DATA.bisData_VECTOR.projects[find_project_INDEX].subProjects[find_subProject_INDEX].time_Update_including_Objects_SUBPROJECT.time_Update_subProjectSettings = Date.now();

                        //       "  " -  ,        -        
                        if (GLOB_DATA.currentUser_readOnly == dataFromServer.user_Email) {
                            let object_Individual_WasReadEvents_forCurrent_subProject = BisGlobFunctions.get_object_Individual_WasReadEvents_forCurrent_subProject(find_project_INDEX, dataFromServer.subProject_ID);

                            object_Individual_WasReadEvents_forCurrent_subProject.time_wasRead_subProjectSettings = Date.now();

                            console.log("      ");
                        }

                        //  
                        GLOB_DATA.update_bisData_Redux(GLOB_DATA.bisData_VECTOR);
                    }
                }

                break;
            }

        case "set_project_settings":
            {
                let find_project_INDEX = BisGlobFunctions.find_Project_index_in_BD(dataFromServer.project_ID);
                if (find_project_INDEX >= 0) {
                    //   
                    GLOB_DATA.bisData_VECTOR.projects[find_project_INDEX].projectSettings = dataFromServer.projectSettings;

                    //         
                    GLOB_DATA.bisData_VECTOR.projects[find_project_INDEX].time_Update_including_Objects.time_Update_projectSettings = Date.now();

                    //       "  " -  ,        -        
                    if (GLOB_DATA.currentUser_readOnly == dataFromServer.user_Email) {
                        let object_Individual_WasReadEvents_forCurrent_project = BisGlobFunctions.get_object_Individual_WasReadEvents_forCurrent_Project(find_project_INDEX);

                        object_Individual_WasReadEvents_forCurrent_project.time_wasRead_projectSettings = Date.now();

                        console.log("      ");
                    }

                    //  
                    GLOB_DATA.update_bisData_Redux(GLOB_DATA.bisData_VECTOR);
                }

                break;
            }

        case "updateFilesListProject":
            {
                console.log(" LongPoolling updateFilesListProject");
                let find_project_INDEX = BisGlobFunctions.find_Project_index_in_BD(dataFromServer.project_ID);
                if (find_project_INDEX >= 0) {
                    //    
                    GLOB_DATA.bisData_VECTOR.projects[find_project_INDEX].attachedFiles.filesList = dataFromServer.newFilesList;
                    //         
                    GLOB_DATA.bisData_VECTOR.projects[find_project_INDEX].time_Update_including_Objects.time_Update_projectSettings = Date.now();
                    //       "  " -  ,        -        
                    if (GLOB_DATA.currentUser_readOnly == dataFromServer.user_Email) {
                        let object_Individual_WasReadEvents_forCurrent_project = BisGlobFunctions.get_object_Individual_WasReadEvents_forCurrent_Project(find_project_INDEX);
                        object_Individual_WasReadEvents_forCurrent_project.time_wasRead_projectSettings = Date.now();
                        console.log("      ");
                    }
                    //  
                    GLOB_DATA.update_bisData_Redux(GLOB_DATA.bisData_VECTOR);
                }
                break;
            }

        case "wasDeleted_one_Project":
            {
                //      , 
                // console.log(" requestService_forLongPulling --- was_deleted_one_Project, dataFromServer= ");
                // console.log(dataFromServer);

                BisGlobFunctions.deleteOneProject_fromLocalData(dataFromServer.project_ID);
                break;
            }

        case "wasDeleted_one_subProject":
            {
                //      , 
                // console.log(" requestService_forLongPulling --- was_deleted_one_subProject, dataFromServer= ");
                // console.log(dataFromServer);
                BisGlobFunctions.deleteOne_subProject_fromLocalData(dataFromServer.project_ID, dataFromServer.subProject_ID);
                break;
            }


    }
}


//===============================
export default requestService_forLongPulling;



